body {
  margin: 0;
}

body .block {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

body .block .stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

body .block .stopwatch__min-hand {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: rotation-minute;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 100%);
}

body .block .stopwatch__sec-hand {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: rotation-second;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 10px);
}

body .block .stopwatch__circle {
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body .block .stopwatch--speed-up .stopwatch__sec-hand {
  animation-duration: 10s;
}

body .block .stopwatch--speed-up .stopwatch__min-hand {
  animation-duration: 600s;
}

@keyframes rotation-second {
  from {
    transform: translate(-50%, 10px)rotate(0);
  }

  to {
    transform: translate(-50%, 10px)rotate(360deg);
  }
}

@keyframes rotation-minute {
  from {
    transform: translate(-50%, 100%)rotate(0);
  }

  to {
    transform: translate(-50%, 100%)rotate(360deg);
  }
}
